// src/hooks/useLogin.js
import { useNavigate } from "react-router-dom";
import { apiClient } from "../service/axiosConfig";
import { useGlobalState } from "../context/GlobalStateContext";
import Cookies from "js-cookie"; //   

export function useLogin() {
  const navigate = useNavigate();
  const { updateGlobalState, globalState } = useGlobalState(); //    
  let isProcessing = false; // API    

  const handleKakaoLogin = async (code) => {
    if (isProcessing) return; //     
    isProcessing = true;

    try {
      const response = await apiClient.post("/auth/oauth/login", {
        provider: "kakao",
        code,
      });
      console.log(response.data);

      if (response.data.code === 200) {
        //  :   
        updateGlobalState("isLoggedIn", true);
        updateGlobalState("nickName", response.data.data.nickname);

        //  jwt   1  (response.data.data.jwtToken)
        const jwtToken = response.data.data.jwtToken;
        Cookies.set("jwt", jwtToken, {
          expires: 1 / 24,
          secure: true,
          sameSite: "Strict",
        }); // 1/24 = 1

        //     
        navigate("/");
      } else {
        alert(" .  .");
        navigate("/"); //    
      }
    } catch (error) {
      console.error("    :", error);
      alert(" .  .");
    } finally {
      isProcessing = false; //     
    }
  };

  return { handleKakaoLogin };
}
