import React, { useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { Sidebar } from "primereact/sidebar";
import { Button } from "primereact/button";
import { useGlobalState } from "../context/GlobalStateContext";
import Cookies from "js-cookie"; //   
import "primereact/resources/primereact.min.css";
import "primeicons/primeicons.css";
import "./Header2.scss";

function Header() {
  const [isSidebarVisible, setIsSidebarVisible] = useState(false); //  
  const { globalState, updateGlobalState } = useGlobalState(); //     
  const location = useLocation(); //     useLocation 

  const handleLogout = () => {
    // JWT   
    Cookies.remove("jwt"); //    
    //     
    updateGlobalState("isLoggedIn", false);
    updateGlobalState("nickName", "");
  };

  return (
    <header className="header">
      <div className="header__logo">
        <Link to="/" className={location.pathname === "/" ? "active" : ""}>
          <img src={require("../assets/logo.png")} alt="" />
        </Link>
      </div>

      {/*     */}
      <nav className="header__nav header__nav--large">
        <Link to="/" className={location.pathname === "/" ? "active" : ""}>
          <span className="icon pi pi-home"></span>
        </Link>
        <Link
          to="/search"
          className={location.pathname === "/search" ? "active" : ""}
        >
          <span className="icon pi pi-search"></span>
        </Link>
        <Link
          to="/stats"
          className={location.pathname === "/stats" ? "active" : ""}
        >
          <span className="icon pi pi-chart-bar"></span>
        </Link>
        <span
          className="header__menu-toggle icon pi pi-bars"
          onClick={() => setIsSidebarVisible(true)}
        ></span>
      </nav>

      {/* Sidebar     */}
      <Sidebar
        visible={isSidebarVisible}
        onHide={() => setIsSidebarVisible(false)}
        position="right"
        className="sidebar"
      >
        <div className="sidebar__header">
          <img
            src={require("../assets/logo.png")}
            alt=""
            className="logo"
          />
          <div className="description">   </div>
        </div>

        <nav className="sidebar__nav">
          <Link
            to="/"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/" ? "active" : ""}
          >
            <span className="icon pi pi-home"></span> 
          </Link>
          <Link
            to="/search"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/search" ? "active" : ""}
          >
            <span className="icon pi pi-search"></span>  
          </Link>
          <Link
            to="/stats"
            onClick={() => setIsSidebarVisible(false)}
            className={location.pathname === "/stats" ? "active" : ""}
          >
            <span className="icon pi pi-chart-bar"></span>  
          </Link>
          {/*      */}
          {globalState.isLoggedIn ? (
            <Link
              onClick={() => {
                setIsSidebarVisible(false);
                handleLogout();
              }}
            >
              <span className="icon pi pi-sign-out"></span> 
            </Link>
          ) : (
            <Link
              to="/login"
              onClick={() => setIsSidebarVisible(false)}
              className={location.pathname === "/login" ? "active" : ""}
            >
              <span className="icon pi pi-sign-in"></span> 
            </Link>
          )}
        </nav>
      </Sidebar>
    </header>
  );
}

export default Header;
