// src/App.js
import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { GlobalStateProvider } from "./context/GlobalStateContext"; // 
import LoadingSpinner from "./components/LoadingSpinner"; // 
import Layout from "./layout/Layout";
import Main from "./pages/Main";
import Search from "./pages/Search/Search"; // Search  import
import Stats from "./pages/Stats/Stats"; // Stats  import
import Stats2 from "./pages/Stats/Stats2"; // Stats  import
import Info from "./pages/Info"; // Search  import
import Login from "./pages/Login/Login"; // Search  import
import KakaoRedirect from "./pages/Login/KakaoRedirect"; // Search  import
import "./App.scss"; // SCSS  import
import "primereact/resources/themes/saga-blue/theme.css"; //  CSS
import "primereact/resources/primereact.min.css"; // Core CSS
import "primeicons/primeicons.css"; //  CSS
import "primeflex/primeflex.css"; // PrimeFlex  CSS ( )

function App() {
  return (
    <GlobalStateProvider>
      <Router>
        <Layout>
          <LoadingSpinner />
          <Routes>
            <Route path="/" element={<Main />} />
            <Route path="/search" element={<Search />} />
            <Route path="/Stats" element={<Stats />} />
            <Route path="/Stats2" element={<Stats2 />} />
            <Route path="/Login" element={<Login />} />
            <Route path="/auth/login/callback" element={<KakaoRedirect />} />
          </Routes>
        </Layout>
      </Router>
    </GlobalStateProvider>
  );
}

export default App;
